<template>
  <div class="download_page">
    <div v-if="downloadLink">
      <section>
          <div style="padding:0;font-size: 18px;color: #f56c6c;">
            <p>下载前请删除原安装包</p>
          </div>
          <div style="padding:0">
          <p>因苹果公司的政策原因，在iOS系统中首次安装企业版应用时会出现"未受信任的企业级开发者"提示</p>
          </div>
          <div style="padding:0 10px 20px;">
              <img class="img-responsive center-block mt-20 mb-20" src="https://static.pgyer.com/static-20190117/images/resovelFlow1_cn.png" style="width:80%;">
              <br>
            <h4 class="font-16">解决办法如下（以iOS11系统为例）：</h4>
              <li style="list-style:none;">1、在手机中打开设置功能，选择"通用"</li>
              <img class="img-responsive center-block mt-20 mb-20" src="https://static.pgyer.com/static-20190117/images/resovelFlow2_cn.png" style="width:80%;">
              <li style="list-style:none;">2、在通用中，选择"描述文件与设备管理"功能</li>
              <img class="img-responsive center-block mt-20 mb-20" src="https://static.pgyer.com/static-20190117/images/resovelFlow3_cn.png" style="width:80%;">
              <li style="list-style:none;">3、在描述文件与设备管理中的企业级应用分类中，选择要安装的企业应用的文件名称（与打开时的提示一致），点击进入</li>
              <img class="img-responsive center-block mt-20 mb-20" src="https://static.pgyer.com/static-20190117/images/resovelFlow4_cn.png" style="width:80%;">
              <li style="list-style:none;">4、进入企业签名页面后，确认企业签名中的公司名称与应用名称后，点击信任"企业签名公司名称"</li>
              <img class="img-responsive center-block mt-20 mb-20" src="https://static.pgyer.com/static-20190117/images/resovelFlow5_cn.png" style="width:80%;">
              <img class="img-responsive center-block mt-20 mb-20" src="https://static.pgyer.com/static-20190117/images/resovelFlow6_cn.png" style="width:80%;">
              <li style="list-style:none;">5、回到桌面，重新打开应用即可使用</li>
              <br>
          </div>
      </section>
      <a :href="downloadLink" target="_blank" class="download_btn" :style="{ bottom: app.hideLayout ? '0' : '50px'}">
        <el-button type="primary" v-if="downloadLink">点击下载IOS客户端</el-button>
      </a>
    </div>
  </div>
</template>

<script>

import { getDownloadLink } from '@/api/common'

export default {
  name: "Download",
  data() {
    return {
      downloadLink: '',
    };
  },
  components: {

  },
  computed: {
		app() { return this.$store.getters.app },
		user() { return this.$store.getters.user },
    localSign() {
      return window.localStorage.getItem('sign');
    },
    localCode() {
      return window.localStorage.getItem('code');
    },
  },
  methods: {
    
  },
  mounted: function() {

      if((this.localSign || this.localCode || this.app.domain.invite_code) && this.user.roles.indexOf('user') == -1) {
        this.$message.error('请先注册或登录账号');
        this.$router.push('/auth/register');
        return ;
      }

      getDownloadLink({
          t: 'ios'
      }).then(json => {
          if(json.code == 200) {
              this.downloadLink = json.list;
          }
      })
  }
};
</script>


