    text-align: center;
    color: #fff;
    padding: 10px 10px 100px;

    li {
        padding: 25px 0;
    }

    .download_btn {
        display: block;
        position: fixed;
        bottom: 50px;
        left: 0;
        right: 0;
        width: 100%;

        button {
          border-radius: 0;
          width: 100%;
          padding: 20px 0;
        }

        a {
            color: #fff;
            text-decoration: none;
            font-size: 16px;
        }
    }
}

   
